"use client";

import { motion } from "framer-motion";

export default function VisionSection() {
  return (
    <section id="vision" className="relative w-full py-24 bg-white">

      {/* subtle geometric background pattern */}
      <div className="absolute inset-0 opacity-[0.06] bg-[url('/pattern-light.png')] bg-repeat pointer-events-none" />

      {/* soft corner glow */}
      <motion.div
        animate={{ x: [-10, 10, -10], opacity: [0.35, 0.55, 0.35] }}
        transition={{ duration: 8, repeat: Infinity, ease: "easeInOut" }}
        className="absolute top-0 right-0 w-[380px] h-[380px] bg-blue-200/40 rounded-full blur-3xl"
      />

      <div className="relative max-w-6xl mx-auto px-6 md:px-12 lg:px-16">

        <div className="grid lg:grid-cols-3 gap-14 items-start">
          
          {/* Left side vertical label */}
          <motion.div
            initial={{ opacity: 0, x: -25 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.7 }}
            className="lg:col-span-1"
          >
            <div className="flex flex-col space-y-4">
              <h2 className="text-4xl md:text-5xl font-bold text-gray-900">
                Our Vision
              </h2>

              {/* accent vertical line */}
              <div className="w-1 h-20 bg-blue-600/60 rounded-full mt-4"></div>
            </div>
          </motion.div>

          {/* Right side content */}
          <motion.div
            initial={{ opacity: 0, y: 25 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.7, delay: 0.1 }}
            className="lg:col-span-2 text-gray-700 text-lg leading-relaxed"
          >
            <p>
              At Smileverse Dentech, our mission is clear â€” to make advanced,
              high-quality dental care accessible and achievable for every Indian.
              By integrating orthodontic science with cutting-edge digital solutions,
              we aim to deliver treatment experiences that are accurate, affordable,
              and sustainable.
            </p>

            <p className="mt-6">
              BraceFree is only the beginning of this journey. We envision a future
              where precision-driven dental innovation is available to all â€” without
              complexity, long waiting times, or financial barriers.
            </p>

            <p className="mt-6">
              Through continuous innovation, expert-led planning, and technology-backed
              workflows, we are shaping the next era of dentistry in India.
            </p>
          </motion.div>
        </div>

      </div>
    </section>
  );
}
