"use client";

import { motion } from "framer-motion";

export default function AboutSection() {
  const features = [
    {
      title: "Orthodontist-Led Treatment",
      desc: "Every case is supervised by certified orthodontists to ensure clinically correct and safe tooth movements."
    },
    {
      title: "Advanced 3D AI-Assisted Planning",
      desc: "Digital simulations and AI-supported planning deliver predictable, accurate alignment results."
    },
    {
      title: "Premium Medical-Grade Material",
      desc: "Internationally approved biocompatible material ensures clarity, comfort, and durability."
    },
    {
      title: "Better Results at Competitive Pricing",
      desc: "Comparable to international brands like Invisalign â€” but priced affordably for the Indian market."
    },
    {
      title: "Safe, Comfortable & Lifestyle-Friendly",
      desc: "Invisible, removable, and easy to maintain â€” ideal for daily use."
    },
  ];

  return (
    <section id="about" className="relative w-full py-24 bg-white overflow-hidden">

      {/* Animated floating blue blur */}
      <motion.div
        animate={{ y: [-10, 10, -10] }}
        transition={{ duration: 6, repeat: Infinity, ease: "easeInOut" }}
        className="absolute -top-24 right-10 w-[400px] h-[400px] bg-blue-100/40 rounded-full blur-3xl"
      />

      <div className="relative max-w-7xl mx-auto px-6 md:px-12 lg:px-16">

        {/* SECTION HEADER */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
          className="max-w-3xl mb-14"
        >
          <h2 className="text-4xl md:text-5xl font-bold text-gray-900">
            About BraceFree Clear Aligners
          </h2>

          <p className="mt-6 text-lg text-gray-700 leading-relaxed">
            BraceFree introduces a new orthodontic standard â€” combining 
            orthodontist-led planning with advanced digital technology for 
            precise, comfortable, and predictable smile transformations.
          </p>

          <p className="mt-4 text-lg text-gray-700 leading-relaxed">
            Every BraceFree treatment plan is designed and monitored exclusively 
            by certified orthodontists, ensuring accuracy and safe tooth movement.
          </p>
        </motion.div>

        {/* FEATURES GRID */}
        <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-8">
          {features.map((feature, i) => (
            <motion.div
              key={i}
              initial={{ opacity: 0, y: 25 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.5, delay: i * 0.12 }}
              className="
                bg-white shadow-[0_5px_25px_rgba(0,0,0,0.06)]
                rounded-2xl p-6 border border-gray-100
                hover:-translate-y-2 hover:shadow-[0_12px_40px_rgba(0,0,0,0.08)]
                transition-all duration-300
              "
            >
              <h3 className="text-lg font-semibold text-gray-900">
                {feature.title}
              </h3>
              <p className="text-gray-700 mt-2 text-sm leading-relaxed">
                {feature.desc}
              </p>
            </motion.div>
          ))}
        </div>

        {/* CLOSING STATEMENT */}
        <motion.p
          initial={{ opacity: 0, y: 25 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6, delay: 0.2 }}
          className="text-lg text-gray-800 mt-14 max-w-4xl leading-relaxed"
        >
          Whether you need mild corrections or a complete smile makeover,
          BraceFree offers the perfect blend of clinical precision, comfort,
          and affordability.
        </motion.p>

      </div>
    </section>
  );
}
